body{
  /* font-family: 'Pretendard', sans-serif; */
  font-family: -apple-system,BlinkMacSystemFont,Malgun Gothic,맑은 고딕,helvetica,Apple SD Gothic Neo,sans-serif;
  font-size: 14px;
  background-color: #2f2f2f;
  color: #f9f7f1;
}
p{
  margin-top: 0;
  margin-bottom: 20px;
}
.head{
  text-align: center;
  position: relative;
}

.head .title {
  white-space: nowrap;
  margin: 0;
  padding-bottom: 15px;
  /* font-family: 'Times New Roman', Times, serif; */
  font-weight: 900;
  font-size: 80px;
  text-transform: uppercase;
}

.subhead{
  text-transform: uppercase;
  border-bottom: 2px solid #444;
  border-top: 2px solid #444;
  padding: 12px 0 12px 0;

}

.column{
  font-size: 14px;
  line-height: 20px;
  width: 17.5%;
  display: inline-block;
  padding: 30px 1%;
  vertical-align: top;
  transition: all .7s;
}
.column + .column { 
border-left: 1px solid #444;
}
.column .headline{
  /* font-family: 'Times New Roman', Times, serif; */
  /* text-align: center; */
  text-align: left;
  line-height: normal;
  display: block;
  margin: 0 auto;
  text-transform: uppercase;
  padding: 0 0 20px;
}
.column .headline a {
  display: flex;
  align-items: center;
  color: inherit;
  text-decoration: none;
}

.column .headline .text {
  font-weight: 700;
  font-size: 21px;
  flex: 1;
  margin-right: 4px;
}

.column .headline a .see-more {
  font-size: 12px;
  color: #aaa;
}

.column .figure {
  margin: 0 0 20px;
}
.column .figcaption{
font-style: italic;
font-size: 12px;
}

.column .toggle-button {
  font-size: 12px;
  background: transparent;
  padding: 7px;
  text-align: center;
  display: block;
  color: white;
  border: 1px solid white;
  width: 100%;
  margin-top: 15px;
}

.media {
  -webkit-filter: sepia(80%) contrast(1) opacity(0.8);
  filter: sepia(80%) grayscale(1) contrast(1) opacity(0.8);
  mix-blend-mode: multiply;
  width: 100%;
}
.article {
  display: block;
  padding: 7px 0;
  color: inherit;
  text-decoration: none;
}

.article .article-title {
  font-weight: 400;
  font-size: 13px;
  margin: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.article:visited .article-title {
  color: #aaa;
}

.footer {
  padding: 30px 0 50px;
  text-align: center;
  color: #666;
  border-top: 2px solid #444;
  font-size: 14px;
}
.footer a {
  color: inherit;
  font-weight: 700;
  text-decoration: none;
}
.footer .copyright {
  font-weight: 700;
}
.footer .request {
  margin-top: 5px;
  font-size: 12px;
}
/*________________________________________________________________________________________________________________________________*/
/*MEDIAQUERIES*/
@media only all and (max-width: 1200px) {
  .column{
      width: 31%;
  }

}
  @media only all and (max-width: 900px) {
  .column{
      width: 47%;
  }

}
@media only all and (max-width: 600px) {
  .column{
      width: 98%;
  }
  .column + .column {
        border-left: none;
        border-top: 1px solid #444;
  }
  .head .title{
      font-size: 40px;
  }
}